.container {
    display: grid;
    grid-template-areas: "t"
    "i"
    "s";
}

@media (min-width: 1024px) {
    .container {
    grid-template-areas: "t i"
    "s i";
        justify-content: center;
        grid-template-columns: 1fr 1fr;
    }
}

.text {
    grid-area: t;
}
.carousel {
    grid-area: s;
}
.image {
    grid-area: i;
}
