@tailwind base;
@tailwind components;
@tailwind utilities;

html, body {
    margin: 0;
    padding: 0;

    font-family: 'Ubuntu', sans-serif;
    color: #FFFFFF;
    background: #1C1D26;
    width: fit-content;
}

.border-gradient {
    border: 3px solid transparent;
    border-radius: 10px;
    background-image: radial-gradient(30.97% 170.33% at 19.03% 18.67%, rgba(149, 173, 244, 0.8) 0%, rgba(100, 136, 240, 0) 100%),
    linear-gradient(77.35deg, rgba(12, 40, 161, 0.8) 0.89%, rgba(100, 135, 239, 0.8) 52.67%, rgba(83, 37, 155, 0.8) 128.34%);
    background-origin: border-box;
    background-clip: content-box, border-box;
    transition: all 0.5s ease;
}



