.container {
    display: grid;
    grid-template-areas:
            "text"
            "inv"
            "btn";
}

@media (min-width: 1024px) {
    .container {
        grid-template-areas:
    "text inv"
    "btn inv";
        grid-template-columns: 1fr 1fr;
    }
}

.text {
    grid-area: text;
}

.inventory {
    grid-area: inv;
}

.btn {
    grid-area: btn;
}
