.themeGrid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas:
    "t t"
    "i i"
    "s s"
}

.img {
    grid-area: i;
}

.tex {
    grid-area: t;
}

.sw {
    grid-area: s;
}

@media (min-width: 1024px) {
    .themeGrid {
        grid-template-areas:
    "i t"
    "i t"
    "i s"
    }
}
